.App {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  display: flex;
  height: 80vh;
  padding: 20px;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
}
.todoForm {
  display: flex;
  justify-content: space-around;
  width: 450px;
}
input {
  width: 80%;
  padding: 10px;
}
button {
  padding: 10px;
  border-radius: 20px;
  margin: 0 5px;
}
.alltodos {
  list-style: none;
  margin: 0;
  padding: 0;
}
.singleTod {
  border-radius: 50px;
  margin: 10px 0px;
  background-color: black;
  color: white;
  padding: 10px 20px;
  display: flex;
  width: 450px;
  align-items: center;
}
.todoText {
  flex: 1;
}
